
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import {mapGetters, mapMutations} from 'vuex'
export default {
  head: {
    title: {
      inner: ''
    }
  },
  data () {
    return {
      score: 0,
      recommend: [
        'static/img/banner1.png',
        'static/img/banner1.png',
        'static/img/banner1.png'
      ],
      advert: [
        'static/img/banner2.png',
        'static/img/banner2.png',
        'static/img/banner2.png'
      ],
      product: {
        type: 0,
        timeOrder: 0,
        priceOrder: 0,
        defaultOrder: 0,
        limit: 8,
        pageIndex: 0
      }
    }
  },
  created () {
    this.getProduct(this)
    this.score = JSON.parse(this.$localStorage.get('userInfo')).userBalance
  },
  computed: {
    ...mapGetters({
      nav: 'getMallNav',
      products: 'getProductList'
    })
  },
  methods: {
    ...mapMutations({
      getProduct: 'getProduct'
    }),
    handleSaveData (item) {
      console.log(item)
      this.$localStorage.set('goods', JSON.stringify(item))
    }
  },
  components: {
    Swiper,
    SwiperItem,
    XImg
  }
}



